  .block{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-around;
  }
  
  .flagCount{
    display: flex;
    justify-content: space-between;
    width: 100px;
    height: 100%;
  }
  .flagCount>div{
    width: 33%;
    height: 100%;
  }
  
  
  .timer{
    display: flex;
    justify-content: space-between;
    width: 100px;
    height: 100%;
  }
  .timer>div{
    width: 33%;
    height: 100%;
  }
  
  .smileBtn{
    width: 50px;
    height: 50px;
  }
  
  .win{
    background: url(assets/nums/win.png) no-repeat center center #ccc;
    background-size: cover;
  }
  .lose{
